#addRoom {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  box-shadow: 4px 4px 8px #575757c7;
  gap: 10px;
  padding: 20px;
  max-width: 40%;
  border-radius: 10px;
  margin: auto;
  background-color: #ccce;
}
.inp{
     padding: 7px;
     border-radius: 10px;
      font-size: 1em;
     border: 2px solid var(--blue);
     box-shadow: 3px 3px 7px #5757578c;
     cursor: pointer;
     ;
}
.resizeInp{
     transition: 0.5s;
     
}
.resizeInp:hover::placeholder{
     transform: scale(1.1);

}
#color{
     background-color: transparent;
     width: 50px;
     height: 50px;
     border: none;
     cursor: pointer;
     transition: 0.5s;

}
#color::-webkit-color-swatch{
     border-radius: 50%;
     border: 4px solid var(--blue) ;

}